朝法事科儀完滿結束。\n          ")])]),_c('td',{attrs:{"width":"81"}},[_c('p',[_vm._v("民雄大士爺廟主辦")])])]),_c('tr',[_c('td',{attrs:{"width":"85"}},[_c('p',[_vm._v("2022/10/14")])]),_c('td',{attrs:{"width":"104"}},[_c('p',[_vm._v("觀世音菩薩出家紀念日朝山")])]),_c('td',{attrs:{"width":"283"}},[_c('p',[_vm._v("\n            農曆9月19日為觀世音菩薩出家紀念日，佛光山於此日舉行朝山禮佛修持活動，信眾由不二門啟行，在莊嚴的地鐘鐘聲引領下，朝禮至大雄寶殿，並由心保住持帶領回向。並透過拜大悲懺，望眾生能效法菩薩之悲心願力。\n          ")])]),_c('td',{attrs:{"width":"81"}},[_c('p',[_vm._v("佛光山主辦")])])]),_c('tr',[_c('td',{attrs:{"width":"85"}},[_c('p',[_vm._v("2022/10/20")])]),_c('td',{attrs:{"width":"104"}},[_c('p',[_vm._v("慈濟歲末祝福")])]),_c('td',{attrs:{"width":"283"}},[_c('p',[_vm._v("\n            每年年底慈濟皆會舉辦歲末祝福活動，由證嚴法師帶領進行座談、開示、誦經祈福。今年首場歲末祝福自臺東靜思堂始，各地慈濟之據點接續輪流舉行，今年之歲末祈福尚以歌曲〈祈禱〉來祝禱，向所有人傳遞祝福之意。\n          ")])]),_c('td',{attrs:{"width":"81"}},[_c('p',[_vm._v("佛教慈濟慈善事業基金會主辦")])])]),_c('tr',[_c('td',{attrs:{"width":"85"}},[_c('p',[_vm._v("2022/11/27-12/04")])]),_c('td',{attrs:{"width":"104"}},[_c('p',[_vm._v("法鼓山大悲心水陸法會")])]),_c('td',{attrs:{"width":"283"}},[_c('p',[_vm._v("\n            水陸法會為漢傳佛教中最重要、殊勝之祭儀，此次大悲心水陸法會共設11座壇場，計有大壇、楞嚴壇、地藏壇、藥師壇、淨土壇、華嚴壇、法華壇等，依不同儀軌設計，供不同法門各自修持，但因疫情僅開放大壇給一般信眾參加，其餘皆以線上為主，每座壇場皆有不同之法師說法，行功德回向、平等普施，普利冥陽。\n          ")])]),_c('td',{attrs:{"width":"81"}},[_c('p',[_vm._v("法鼓山主辦")])])])])])])}]

