',{attrs:{"width":"97"}},[_c('p',[_vm._v("2022搖滾臺中")])]),_c('td',{attrs:{"width":"261"}},[_c('p',[_vm._v("\n            搖滾臺中2007年開始舉辦，目的為讓民眾能夠體現臺中市音樂文化質地的美好，同時運用臺中的地理位置優勢、豐沛的文化元素、優秀的音樂人才，持續將「搖滾臺中」打造成為臺灣流行音樂不可錯過的盛宴。今年適逢15週年，以「青少年」為主題，前兩年受疫情影響只能以線上方式舉辦，今年睽違已久再次舉行實體活動，重回文心森林公園暨圓滿戶外劇場，讓這次活動更具意義。\n          ")])]),_c('td',{attrs:{"width":"103"}},[_c('p',[_vm._v("臺中市政府新聞局主辦")])])]),_c('tr',[_c('td',{attrs:{"width":"92"}},[_c('p',[_vm._v("2022/10/08-10/10")])]),_c('td',{attrs:{"width":"97"}},[_c('p',[_vm._v("2022浪人祭")])]),_c('td',{attrs:{"width":"261"}},[_c('p',[_vm._v("\n            浪人祭為南部地區重要的音樂祭之一，結合「音樂、淨灘」，並透過在地、藝術、特別企劃等形式，傳遞環境循環永續的核心理念，粉絲及旅客可以一邊在熱力音樂下舞動身體，又或是在岸邊悠閒踏浪，是臺灣獨立音樂圈年度盛事。原訂7月初登場，因疫情延期到10月底舉行。\n          ")])]),_c('td',{attrs:{"width":"103"}},[_c('p',[_vm._v("笨道策展有限公司")])])]),_c('tr',[_c('td',{attrs:{"width":"92"}},[_c('p',[_vm._v("2022/10/08-10/10、10/13-10/16")])]),_c('td',{attrs:{"width":"97"}},[_c('p',[_vm._v("2022臺北爵士音樂節")])]),_c('td',{attrs:{"width":"261"}},[_c('p',[_vm._v("\n            臺北爵士音樂節自2007年舉辦以來，成功將臺北爵士音樂節打造成城市音樂品牌，以臺北都會的獨特氛圍，透過國際交流讓臺灣爵士音樂和國際接軌，結合爵士樂國際化、即興、愜意的特性，讓市民能欣賞各地爵士樂曲並進入臺北市民生活中。2022臺北爵士音樂節以「爵士就是生活」為主題，分別臺北流行音樂中心南基地戶外廣場及大安森林公園音樂臺登場。除了音樂創作的多元面向，呈現豐富的樣貌，也首次邀請音樂治療師合作舉辦演講，期許能夠藉由活動帶領民眾認識助人工作的不同面向。在臺北流行音樂中心活動期間，更舉辦了一系列爵士講座，帶領民眾從更多元的角度認識爵士音樂文化。\n          ")])]),_c('td',{attrs:{"width":"103"}},[_c('p',[_vm._v("臺北市政府文化局")])])]),_c('tr',[_c('td',{attrs:{"width":"92"}},[_c('p',[_vm._v("2022/10/22-10/23")])]),_c('td',{attrs:{"width":"97"}},[_c('p',[_vm._v("Taiwan PASIWALI Festival")])]),_c('td',{attrs:{"width":"261"}},[_c('p',[_vm._v("\n            PASIWALI音樂節為東部最重要的音樂節之一，PASIWALI是臺灣原住民族阿美族語，往東方的意思。PASIWALI部落就是位在現今富岡的位置，在臺十一線路旁，就可以看到用木頭搭建的部落入口意象。巴沙哇力部落跟石山部落算是兄弟部落，當初同樣都是從海岸山脈尾端的猴子山社遷徙出來，因為落腳的位置不一樣，分成兩個獨立部落。PASIWALI\n            Festival意指一場長達數十萬公里的南島之舟部落音樂旅程，每年都邀請重量級演出卡司嗨唱臺東。\n          ")])]),_c('td',{attrs:{"width":"103"}},[_c('p',[_vm._v("原住民族委員會")])])]),_c('tr',[_c('td',{attrs:{"width":"92"}},[_c('p',[_vm._v("2022/11/05")])]),_c('td',{attrs:{"width":"97"}},[_c('p',[_vm._v("第十三屆金音創作獎")])]),_c('td',{attrs:{"width":"261"}},[_c('p',[_vm._v("\n            文化部影視及流行音樂產業局主辦的「金音創作獎」，自2010年創立，成為臺灣流行音樂的重要獎項。所有獎項以創作為本質核心，建立「臺灣優先」、「原創優先」、「分樂種不分語言」三大特色，是一個代表臺灣音樂人自由多元、原創力量的重要國際音樂品牌。金音創作獎設置單曲獎項、接受數位發表的音樂作品，設置搖滾、民謠、電音、嘻哈、爵士、節奏藍調、另類流行、跨界或世界音樂等不同音樂風格的獎項，並設置「最佳現場演出獎」及「最佳樂手獎」演出技術相關之獎項，也設置「海外創作音樂獎」獎勵海外華語創作。\n          ")])]),_c('td',{attrs:{"width":"103"}},[_c('p',[_vm._v("文化部影視及流行音樂產業局主辦")])])]),_c('tr',[_c('td',{attrs:{"width":"92"}},[_c('p',[_vm._v("2022/11/03-11/06")])]),_c('td',{attrs:{"width":"97"}},[_c('p',[_vm._v("2022貴人散步音樂節")])]),_c('td',{attrs:{"width":"261"}},[_c('p',[_vm._v("\n            貴人散步音樂節今年已邁入第六年，以臺灣少見的「showcase」形式，年年邀請海內外音樂人與產業人士，與樂迷齊聚臺南，也積極促成音樂人與國際產業鏈的結合。2022年於11月3日登場，演出場域散布臺南市區，環繞著美術館、戲院、藝廊等場域的showcase演出，連續四天獻上近70組國內外音樂人共譜的音樂盛宴，之所以取名為貴人散步音樂節，即希望歌迷們可以用放慢腳步，以散步的形式至各場館欣賞音樂演出，除了音樂之外，同時也可以欣賞臺南古都沉靜的美，此音樂節為臺南具指標性音樂節之一。\n          ")])]),_c('td',{attrs:{"width":"103"}},[_c('p',[_vm._v("合為貴主辦")])])]),_c('tr',[_c('td',{attrs:{"width":"92"}},[_c('p',[_vm._v("2022/12/02-12/04")])]),_c('td',{attrs:{"width":"97"}},[_c('p',[_vm._v("2022漂遊者森林音樂祭")])]),_c('td',{attrs:{"width":"261"}},[_c('p',[_vm._v("\n            2022漂遊者森林音樂祭為全臺最大的戶外音樂祭，假臺中麗寶樂園落羽松林地盛大舉辦。萬坪草原架設巨型雙舞臺搭配、120\n            米 Sky Dream\n            超級摩天輪，打造出情境式大型「野區戶外活動音樂祭」，視覺本體導入賽博龐克，表現後疫情末日世界的樣貌。三天兩夜的露營時光，更邀請百組餐飲、藝文等各領域品牌進駐現場，打造前所未見、專屬於臺灣的音樂祭盛事。\n          ")])]),_c('td',{attrs:{"width":"103"}},[_c('p',[_vm._v("PPAPER主辦")])])])])])])}]

